import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, CircularProgress } from "@rp/react/web/mui/core"
import { KeyboardArrowUp } from "@rp/react/web/mui/icons"

type Props = {
  variant?: "mobile" | "desktop"
}

const LoadingResponseSkeleton = ({ variant }: Props) => {
  const { isMobile: isMobileDevice } = useDeviceInfo()
  const isMobile = variant === "mobile" || isMobileDevice

  return (
    <Box
      sx={{
        border: "1px solid",
        borderColor: "outline.border",
        p: 2,
        py: 1.5,
        borderRadius: 2,
        ml: isMobile ? 2 : 7,
        mt: 1,
        mr: isMobile ? 2 : 0,
      }}
    >
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
        }}
      >
        <Box
          sx={{
            display: "flex",
            gap: 1,
            color: "text.secondary",
            alignItems: "center",
          }}
        >
          <Box>
            <CircularProgress color="inherit" size={16} />
          </Box>
        </Box>

        <KeyboardArrowUp
          sx={{
            color: "text.secondary",
          }}
        />
      </Box>
    </Box>
  )
}
export { LoadingResponseSkeleton }
